

import {useEffect, useState} from 'react'
import '../css/cartshop.css'


function setValueObj (data) {
    for ( let item of data) {
        if( typeof localStorage[item.id] != "undefined") {
            item.order = true;
            item.quantity =  Number(localStorage.getItem(item.id));
            item.subTotal=item.quantity*item.price;
        }
    }
}
function subTotal(sp){
    const id = sp.id;
    var sl = document.querySelector(`#${id} .quantity input`).value;
    var totalPrice = sl*sp.price;
    const subTotal = document.querySelector(`#${id} .subtotal`);
    subTotal.innerText = new Intl.NumberFormat('de-DE', { style: 'currency', currency: 'USD' }).format(totalPrice);
    localStorage.setItem(sp.id, sl);
}
function calTotal(data){
    const elementTotal = document.querySelector("#total span");
    var total=0;
    for(let sp of data){
        if(typeof localStorage[sp.id] != 'undefined'){
            let sl = localStorage.getItem(sp.id);
            total += sl*sp.price;
        }
    }
    elementTotal.innerText = new Intl.NumberFormat('de-DE', { style: 'currency', currency: 'USD' }).format(total);
}

function handleChange(sp, data){
    subTotal(sp);
    calTotal(data);
}

function totalOne(data){    // de tinh total trong lan dau tien chay
    var total=0;
    for(let sp of data){
        if(typeof localStorage[sp.id] != 'undefined'){
            let sl = localStorage.getItem(sp.id);
            total += sl*sp.price;
        }
    }
    return total;
}
function handleDelItem(id, data){
    const item = document.getElementById(id);

    const table = document.querySelector('#table tbody')
    table.removeChild(item)

    localStorage.removeItem(id);

    for( let sp of data){
        if(sp.id == id){
            sp.quantity = 0;
            sp.order = false;
        }
    }

    calTotal(data)


}
function CartItem({sp, data}){ //component
    if(sp.order === false) return;
    return (
        <tr key={sp.id} id={sp.id}>
            <td className="image">
                <img src={sp.img} alt="" />
            </td>
            <td className="name">{sp.name}</td>
            <td className="price">{sp.price}.00$</td>
            <td className="quantity">
                <input type="number" min="1" defaultValue={sp.quantity} onChange={()=>{handleChange(sp,data)}}/>
            </td>
            <td className="subtotal">{sp.subTotal}.00$</td>
            <td className="x-btn-js">
                <i className="far fa-times-circle" onClick={()=>{handleDelItem(sp.id, data)}}></i>
            </td>
        </tr>
    )
}
function Cartshop (){   //component
    const [data, setData] = useState([{}]);
    useEffect(()=>{
        fetch('https://server-react-s.herokuapp.com/api_shop')
        .then(res=>res.json())
        .then(newdata=>{
            newdata.map(item=>{
                item.quantity=0;
                item.order=false;
            })
            setValueObj(newdata);
            setData(newdata)
        })
    }, [])
    return (
        <main id="cart">
            <div className="head">
                <h1>Cart</h1>
                <p>
                    Home
                    <i className="fas fa-angle-right"></i>
                    Cart
                </p>
            </div>
    
            <table id={'table'}>
                <thead>
                    <tr>
                        <td className="image"></td>
                        <td className="name">Product</td>
                        <td className="price">Price</td>
                        <td className="quantity">Quantity</td>
                        <td className="subtotal">Subtotal</td>
                        <td className="x-btn-js"></td>
                    </tr>
                </thead>
                <tbody>
                    {
                        data.map(sp=>(sp.order && <CartItem 
                            sp={sp}
                            data={data}
                            />
                        ))
                    }
                </tbody>
            </table>
            <div id="total">
                <h1>Total:
                    <span id="add-text" style={{padding: 20}}>    {
                    new Intl.NumberFormat('de-DE', { style: 'currency', currency: 'USD' }).format(totalOne(data))
                    }
                    </span>
                </h1>
            </div>
        </main>
    )
}
export default Cartshop;