
import {useState, useEffect} from "react"

import "../css/shop.css"

function handleClickBuy(item){
    addCart(item.id)
    alert(`Bạn đã đặt bánh ${item.name} \n Vui lòng vào giỏ hàng để tùy chỉnh số lượng`)
}

function addCart (masp) {
    if ( typeof localStorage[masp] === "undefined") {
        localStorage.setItem(masp,1);
    } else {
        let num = Number(localStorage.getItem(masp)) + 1;
        localStorage.removeItem(masp);
        localStorage.setItem(masp,num);
    }
}
function handleMouseEnter(id) {
    let bg = document.querySelector(`#${id} .bg`);
    let img = document.querySelector(`#${id} img`);
    bg.style.animation = "shopbg ease 0.5s forwards";
    img.style.animation = "shop-img ease 0.5s forwards";
}
function handleMouseLeave (id) {
    let bg = document.querySelector(`#${id} .bg`);
     let img = document.querySelector(`#${id} img`);
     bg.style.animation = "shopbg-a ease 0.5s forwards";
     img.style.animation = "shop-img-a ease 0.5s forwards";
 }



function SpItem ({item}){//component
    return (
        <li key={item.id} id={item.id} onMouseLeave={()=>{handleMouseLeave(item.id)}} onMouseEnter={()=>{handleMouseEnter(item.id)}}>
            <div className="sp-main">
                <div>
                    <div className="img">
                        <img src={item.img} alt="" />
                        <div className="bg"></div>
                    </div>
                </div>
                <div className="content">
                    <h3>{item.name}</h3>
                    <p>{item.description}</p>
                    <div className="content-bt">
                        <h2>{item.price}$</h2>
                        <div className="cart-icon buy-js" onClick={()=>{handleClickBuy(item)}}>
                            <i className="fas fa-shopping-basket"></i>
                        </div>
                    </div>
                </div>
            </div>
        </li>
    )
}

function Shop (){//component
    const [data, setData] = useState([{}]);
    useEffect(()=>{
        fetch('https://server-react-s.herokuapp.com/api_shop')
        .then(res=>res.json())
        .then(data=>{
            setData(data);
            console.log(data)
        })
        .catch((error)=>{
            console.log(error)
        })
    }, [])
    console.log("re render")


    return (
        <main id="shop">
            <div className="head" key={1}>
                <div className="head-content">
                    <h1>Shop</h1>
                    <p>
                        Home
                        <i className="fas fa-angle-right"></i>
                        Shop
                    </p>
                </div>
            </div>
            <ul key={2}>
                {data.map((item)=>{
                    return (
                        <SpItem 
                        item={item}
                        />
                    )
                })}
            </ul>
        </main>
    )
}
export default Shop;
